import lrz from 'lrz'	
import back from '@/components/back'

export default {
    components:{
        back
    },
	data () { //引入PopupPicker组件 引入提现路由
		return {
			DrId: this.$route.params.id,
			name: '', //姓名
			cardName: '', //银行名称
			bank: '', //开户行
			number: '', //卡号
			verify: false,
			verifyTxt: '',
			type: 'cancel',
			BankImg: '',
			start: this.$route.params.start,
			DrBankCardId: this.$route.params.DrBankCardId,
			appleTn: false,
			AndroidTn: false,
			baName: this.$route.params.start == 1 ? '添加银行卡' : '修改银行卡',
			bl: {
				DisplayPath: '',
				SavePath: ''
			},
		}
	},
	mounted:function(){
		if(this.start == 2){
			this.$get('/PlatFormAPI/Doctor/QueryDoctorBankCardByDrId',{
				DrId : this.DrId
			}).then(o => {
				if(o != null && o != undefined && o != ''){
					
					console.log(o)
					this.name = o.Name
					this.number = o.CardNo
					this.cardName = o.BankName
					this.bank = o.OpenBank
					this.bl.SavePath = o.BankImg
				}
			})
		}
	},
	methods: {
		imgUpdate (e) {
			const that = this
			const file = e.target.files[0]
			if (file){
				lrz(file).then(rst => {
					var reader = new FileReader();
					reader.readAsDataURL(file);
					const that = this
					reader.onload = function (e) {
						that.er = this.result.split('base64,')[1]
						that.$post('PlatFormAPI/PatientArchives/ReportImageUpload',{
							Base64Str: this.result.split('base64,')[1]
						}).then(o => {
							that.bl.DisplayPath = o.DisplayPath
							that.bl.SavePath = o.SavePath
						})
					}
				})
				e.target.value = null
			}
		},
		getImg(target){
			this.$bridge.callhandler('APP', 'Img', (data) => {
				// this[target].push()
				const DisplayPath = data.split(',')[0].split('=')[1]
				const SavePath = data.split(',')[1].split('=')[1].split('}')[0]
				this[target].DisplayPath = DisplayPath;
				this[target].SavePath = SavePath;
			})
		},
		addCardFn () {
			let inputNull = /\S/
			let msg = [
				{name: this.name,msg: '真实姓名'},
				{name: this.cardName,msg: '银行名称'},
				{name: this.bank,msg: '开户行'},
				{name: this.number,msg: '卡号'}
			]
			for(let i = 0;i < msg.length;i++){
				if(!inputNull.test(msg[i].name)){
					this.verifyTxt = msg[i].msg + '是必填项'
					this.verify = true
					return
				}
			}
			let arr = null
			if(this.start == 1){
				arr = {
					DrId: this.DrId,
					CardNo: this.number,
					Name: this.name,
					BankName: this.cardName,
					OpenBank: this.bank,
					BankImg: this.bl.SavePath
				}
			}else{
				arr = {
					DrBankCardId: this.DrBankCardId,
					DrId: this.DrId,
					CardNo: this.number,
					Name: this.name,
					BankName: this.cardName,
					OpenBank: this.bank,
					BankImg: this.bl.SavePath
				}
			}
			this.$post('/PlatFormAPI/Doctor/SaveDoctorBankCard',arr).then(o => {
				if(o){
					this.type = '1s'
					if(this.start == 1){
						this.verifyTxt = '添加成功'
					}else{
						this.verifyTxt = '修改成功'
					}
					this.verify = true
					let that = this
					setTimeout(function () {
						that.$router.push({
							path: '/Center/card',
							name: 'card',
							params: {
								id: that.DrId,
							}
						})
					},500);
				}
			})
		},
		toUpdate(){
				const browser = navigator.userAgent
				if(browser.indexOf('iPhone') !== -1){
					this.appleTn = true;
					this.AndroidTn = false;
				}
				else{
					this.appleTn = false;
					this.AndroidTn = true;
				}
				
		}
	},
	created(){
		//this.getCard()DrBankCardId
		/*if(this.DrBankCardId == undefined){
			this.$router.push({
				path: '/Center/card',
				name: 'card',
				params: {
					id: this.DrId
				}
			})
		}*/
		this.toUpdate();
	}
}



